import React from "react";
import "./404.css";

const _404 = () => {
  return (
    <div className="not_found">
      <h1>Error 404</h1>
      <h3>The page you were looking for could not be found!</h3>
      <h3>Please check the url and try again!</h3>
    </div>
  );
};

export default _404;
